.jumbotron {
    /* height: 1000px; */
    background-image: -webkit-gradient(linear, 
                    left top, right bottom, 
                    from(rgba(255, 255, 255, 0.6)), 
                    to(rgba(255,255,255,0.8))),
                      url("https://res.cloudinary.com/chu327/image/upload/v1533690366/portfolio/markus-spiske-768763-unsplash.jpg");
    background-image: -webkit-linear-gradient(left top, 
                    rgba(255, 255, 255, 0.6), 
                    rgba(255,255,255,0.8)),
                      url("https://res.cloudinary.com/chu327/image/upload/v1533690366/portfolio/markus-spiske-768763-unsplash.jpg");
    background-image: -o-linear-gradient(left top, 
                    rgba(255, 255, 255, 0.6), 
                    rgba(255,255,255,0.8)),
                      url("https://res.cloudinary.com/chu327/image/upload/v1533690366/portfolio/markus-spiske-768763-unsplash.jpg");
    background-image: linear-gradient(to right bottom, 
                    rgba(255, 255, 255, 0.6), 
                    rgba(255,255,255,0.8)),
                      url("https://res.cloudinary.com/chu327/image/upload/v1533690366/portfolio/markus-spiske-768763-unsplash.jpg");
    background-attachment: fixed;
    background-size: cover;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    padding: 23rem 0rem 30rem 0rem;
    width: 100%;
    margin: 0;
}

.heading-name {
    font-family: 'Heebo', sans-serif;
    font-size: 6rem;
    margin-bottom:0;
    text-transform: uppercase;
    letter-spacing: 2px;
    -webkit-transform: scale(1, 0.9);
        -ms-transform: scale(1, 0.9);
            transform: scale(1, 0.9);
}

.heading-description {
    font-family: 'Heebo', sans-serif;
    font-weight: 300;
    letter-spacing: 1px;
    padding-top: 2rem;
}

.button {
    width: 70%;
    margin-top: 15rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}

#button-portfolio {
    background-color: black;
    height: 40px;
    width: auto;
    color: white;
    font-size: 13px;
    text-transform: uppercase;
    font-family: 'Heebo', sans-serif;
    font-weight:300;
    letter-spacing: 1.2px;
    -webkit-transform: scale(1, 1.2);
        -ms-transform: scale(1, 1.2);
            transform: scale(1, 1.2);
   
    cursor: pointer;
    -webkit-transition: all .5s ease;
    -o-transition: all .5s ease;
    transition: all .5s ease;
}

#button-portfolio:focus {
    outline: 0;
}
#button-portfolio:hover{
    background-color: inherit;
    border: solid;
    border-width: 1px;
    border-color: black;
    color: black;
}

@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
    .heading-name {
        font-size: 3rem;
    }
    .heading-description {
        font-size: 1.3rem;
    }
    #button-portfolio {
        font-size: 0.7rem;
    }
}


