.contact {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    background-color: rgb(255,255,255);
    padding: 7rem 0 5rem 0;
    position: relative;
    width: 100%;
}

.contact-title {
    font-size: 2.5rem;
    text-transform: uppercase;
    color: black;
    text-align: center;
    letter-spacing: 0.7px;
    font-family: 'Heebo', sans-serif;
    font-weight: 400;
}

.contact-info {
    width: 70%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.contact-social {
    padding: 2rem;
    display: -ms-flexbox;
    display: flex;
}

.contact-link {
    text-decoration: none;
}

.contact-image {
    height: 4rem;
    width: 4rem;
    margin: 0 2rem;
    cursor: pointer;
    color: black;
    font-size: 5rem;
}

.contact-linkedIn {
    font-size: 3rem;
    color: white;
    background-color: black;
    padding: 1rem;
    border-radius: 50%;
}

.contact-table {
   font-size: 1.5rem;
   text-align: left;
   padding: 2rem;
}

.contact-detail {
    font-family: 'Heebo', sans-serif;
    letter-spacing: 1.3px;
    -webkit-transform: scale(1, 1.1);
        -ms-transform: scale(1, 1.1);
            transform: scale(1, 1.1);
    color:black;
    font-weight: 300;
}

.contact-resume {
    padding: 2rem;
}

.contact-button {
    height: 4rem;
    width: 18rem;
    color: black;
    background-color: white;
    border: 1px solid black;
    font-size: 1.3rem;
    font-family: 'Heebo', sans-serif;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    cursor: pointer;
    -webkit-transition: all .5s ease;
    -o-transition: all .5s ease;
    transition: all .5s ease;
}

.contact-button:hover {
    background-color: black;
    color: white;
    font-weight: 300;
}

.contact-button:focus {
    outline: 0;
} 


@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
    .contact-title {
        font-size: 2rem;
    }
    .contact-detail {
        font-size: 1.4rem;
    }
}


