@-webkit-keyframes slideIn {
    0% { width: 0;}
    50% { width: 75px; }
    100% { width: 150px; }
} 

@keyframes slideIn {
    0% { width: 0;}
    50% { width: 75px; }
    100% { width: 150px; }
} 

.navbar-side {
    background-color: black;
    height: 100%;
    position: fixed;
    z-index: 1000;
    -webkit-animation-name: slideIn;
            animation-name: slideIn;
    -webkit-animation-duration: .1s;
            animation-duration: .1s;
    -webkit-transition: all .5s ease-in-out;
    -o-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
}

.navbar-list {
    height:100%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    margin: 0;
    padding-top: 5rem;
}

.navbar-button {
    color: white;
    font-size: 15px;
    font-weight: 300;
    letter-spacing: 1px;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    padding: 1rem;
    display: inline-block;
    cursor: pointer;
    text-decoration: none;
}

.navbar-button:hover {
    background-color: white;
    color: black;
    width: 87%;
}

#button-menu {
    background-color:inherit;
    border: none;
    margin-top: 10px;
    margin-left: 10px;
    font-size: 30px;
    color: gray;
    cursor: pointer;
    position: fixed;
}

#button-close {
    background-color: inherit;
    color: grey;
    float: right;
    border:none;
    margin-top: 5px;
    margin-right: 5px;
    font-size: 25px;
    cursor: pointer;
}
  
#button-menu,
#button-close:focus {
    outline: 0;
}

.reveal {
    -webkit-transform: translateX(0%);
    -ms-transform: translateX(0%);
    transform: translateX(0%);
    -webkit-transition: 300ms ease;
    -o-transition: 300ms ease;
    transition: 300ms ease;
}

@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
    .navbar-button {
        font-size: 0.7rem;
    }
    .navbar-side {
      padding-top: 3rem;
    }
    .navbar-list {
        padding-top: 3rem;
    }
}


