.about {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack:center;
        justify-content:center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 100%;
}

.about-image {
    height: 40rem;
    width: 400px;
    margin-right: 5rem;
}

.about-info {
    width: 500px;
    height: 100%
}

.about-title {
    font-size: 2.5rem;
    text-transform: uppercase;
    color: grey;
    text-align: center;
    letter-spacing: 0.7px;
    font-family: 'Heebo', sans-serif;
}

.about-description {
    font-size: 1.4rem;
    letter-spacing: 1px;
    line-height: 2.5rem;
    font-family: 'Heebo', sans-serif;
    color: grey;
    font-weight: 400;
}

@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
    .about {
        text-align: left;
    }
    .about-image {
        margin-right: 0;
        height: 35rem;
        width: 100%;
    }
    .about-title {
        font-size: 2rem;
    }
    .about-description {
        padding-left: 0.5rem;
    }
}



